import React from 'react';
import {  PanelHeader,  Avatar, Div, HeaderButton, List, Cell, Snackbar, Group, InfoRow, Progress, Tabs, TabsItem} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
var axios = require('axios');



class Questions extends React.Component {
constructor(props){
super(props)

this.state ={
activeTab:'questions'

}
}
  connect(){
 axios.get("http://localhost/index.php?type=get_all")
   .then(res => {

      console.log(res)
      });
}



	render(){
		return(
			<Div>
			     <Tabs theme="light">
                <TabsItem
                 onClick={() => this.setState({ activeTab: 'questions' })}
                  selected={this.state.activeTab === 'questions'}
                >
                  Задания
                </TabsItem>
                <TabsItem
                     onClick={() => this.setState({ activeTab: 'rr' })}
                  selected={this.state.activeTab === 'rr'}

                >
                  Рейтинг
                </TabsItem>
          </Tabs>

          {this.state.activeTab === "questions" ? <Div><p>KLLLLLLLLLLLL</p><button onClick={this.connect}>bg</button></Div>:null}



              </Div>
			);
	}
}
export default Questions;
