import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner, Epic, PanelHeader, Panel, Tabbar, TabbarItem, Avatar, Div, HeaderButton, List, Cell, Snackbar, Button} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Polls from 'react-polls';
var axios = require('axios');


class Poll extends React.Component {

constructor(props){
super(props);

this.state = {
  selectedPlace:this.props.selectedPlace,
  pollAnswers:this.props.Poll,
  pollQuestion:this.props.selectedPlace.pollQuestions
}
}
  // Handling user vote
  // Increments the votes count of answer when the user votes
  handleVote = voteAnswer => {
     const newPollAnswers = this.state.pollAnswers.map(answer => {
       if (answer.option === voteAnswer) answer.votes++
       return answer
     })
     const rt = JSON.stringify(newPollAnswers);
     console.log(rt);
     axios.post("https://www.questsinpocket.com/vk/index.php?type=set_poll&id_marker="+this.state.selectedPlace.id_marker+"&poll="+rt);
     this.setState({
       pollAnswers: newPollAnswers
     })
   }



  render(){


   return(
    <div>
       <Polls question={this.state.pollQuestion} answers={this.state.pollAnswers} onVote={this.handleVote} />
     </div>

	 	);
	}
}

export default Poll;
