import React from 'react';
import {Panel, Div, Button, Cell, Avatar} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
VKStories.init(connect);




//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }

    ReturnMap = () => {
      this.props.theStory();
    }

  GenerateStory = () => {
      VKStories.generateStoryFromTemplate(require("../img/Design-story.png"), [
      {
        font: "45px Arial",
        align: "center",
        color: "#FFFFFF",
        value: "Я был здесь: " + this.props.selectedPlace.name + ". А ты?",
        x: 540,
        y: 1133,
        }
     ])
     .then( (story) => {
        VKStories.shareStory(7150376, story, { add_to_news: true })
        this.ReturnMap();
    })
}
     

   

  render() {
    return(
      <Div align="center">
       <Avatar src={this.props.fetchedUser.photo_200} size={150} />
       <Cell id="name">{this.props.fetchedUser.first_name+" "+this.props.fetchedUser.last_name}</Cell>
      <Div align="center">
      <Button level="commerce" size="xl" onClick={this.GenerateStory}>Опубликовать</Button>
      <Button style={{"marginTop":"1.5%"}} level="destructive" size="xl" onClick={this.ReturnMap}>Отмена</Button>
      </Div>

      </Div>
   );
  }

}

export default Modal;